import * as React from 'react'
import {
  Grid,
  IGridHeader,
  IGridColumn,
  _,
  gridUtil,
} from '@zen-components/grid/gridExports'

import * as headersConfig from './headersConfig'

export function buildHeaders(showAdvanced) {
  let headers: IGridHeader[] = [
    {
      check: true,
    },
    {
      label: 'ID',
      dataItem: 'ibomid',
      width: 40,
      tag: 'id',
    },
    {
      label: 'Product',
      dataItem: 'parentname',
      tag: 'parent',
    },
    {
      label: 'Name',
      dataItem: 'compname',
    },
    {
      label: 'Units for Assembly',
      dataItem: 'upa',
      tooltip: '',
    },
  ]

  headers = headersConfig.filterHeaders(headers, showAdvanced)

  return headers
}
